<template>
  <section class="cta-container">
    <h5>35,000+ already joined</h5>
    <h1>Stay up-to-date with what we’re doing</h1>
    <div class="cta-contact-container">
      <ValidationObserver v-slot="{ handleSubmit }">
        <form @submit.prevent="handleSubmit(onSubmit)"></form>
        <ValidationProvider
          name="Email"
          rules="required|email"
          v-slot="{ errors }"
        >
          <input
            type="email"
            placeholder="Enter your email address"
            v-model="email"
          />
          <p class="handle-error">{{ errors[0] }}</p>
        </ValidationProvider>
        <button class="btn-contact">Contact Us</button>
      </ValidationObserver>
    </div>
  </section>
</template>

<script>
export default {
  data() {
    return {
      email: null,
    };
  },
  methods: {
    onSubmit() {
      console.log(this.email);
    },
  },
};
</script>
