<template>
  <section class="features-cmp-container">
    <div class="features-header-container">
      <h1>Features</h1>
      <p>
        Our aim is to make it quick and easy for you to access your favourite
        websites. Your bookmarks sync between your devices so you can access
        them on the go.
      </p>
    </div>
    <div class="features-content-container">
      <div class="features-btns-container">
        <button class="btn" :class="selectedSimple" @click="selectFeature(1)">
          Simple Bookmarking
        </button>
        <button class="btn" :class="selectedSpeedy" @click="selectFeature(2)">
          Speedy Searching
        </button>
        <button class="btn" :class="selectedEasy" @click="selectFeature(3)">
          Easy Sharing
        </button>
      </div>
      <div class="feature-container">
        <div class="feature1" v-if="feature1">
          <div class="feature-img">
            <img src="../assets/imgs/illustration-features-tab-1.svg" />
          </div>
          <div class="feature-content">
            <h1 class="feature-header">Bookmark in one click</h1>
            <p class="feature-txt">
              Organize your bookmarks however you like. Our simple drag-and-drop
              interface gives you complete control over how you manage your
              favourite sites.
            </p>
            <button class="btn btn-info">More Info</button>
          </div>
        </div>
        <div class="feature2" v-if="feature2">
          <div class="feature-img">
            <img src="../assets/imgs/illustration-features-tab-2.svg" />
          </div>
          <div class="feature-content">
            <h1 class="feature-header">Intelligent search</h1>
            <p class="feature-txt">
              Our powerful search feature will help you find saved sites in no
              time at all. No need to trawl through all of your bookmarks.
            </p>
            <button class="btn btn-info">More Info</button>
          </div>
        </div>
        <div class="feature3" v-if="feature3">
          <div class="feature-img">
            <img src="../assets/imgs/illustration-features-tab-3.svg" />
          </div>
          <div class="feature-content">
            <h1 class="feature-header">Share your bookmarks</h1>
            <p class="feature-txt">
              Easily share your bookmarks and collections with others. Create a
              shareable link that you can send at the click of a button.
            </p>
            <button class="btn btn-info">More Info</button>
          </div>
        </div>
      </div>
    </div>
    <div class="background-color"></div>
  </section>
</template>

<script>
export default {
  data() {
    return {
      feature1: true,
      feature2: false,
      feature3: false,
    };
  },
  methods: {
    selectFeature(num) {
      if (num === 1) {
        this.feature1 = true;
        this.feature2 = false;
        this.feature3 = false;
      } else if (num === 2) {
        this.feature1 = false;
        this.feature2 = true;
        this.feature3 = false;
      } else {
        this.feature1 = false;
        this.feature2 = false;
        this.feature3 = true;
      }
    },
  },
  computed: {
    selectedSimple() {
      return { "btn-selected": this.feature1 };
    },
    selectedSpeedy() {
      return { "btn-selected": this.feature2 };
    },
    selectedEasy() {
      return { "btn-selected": this.feature3 };
    },
  },
};
</script>

